import { inject, injectable, named, postConstruct } from 'inversify'
import {
  AuthenticationError,
  IdentityProviderServiceSymbol,
  IIdentityProviderService,
} from '@flowda-projects/v1-flowda-types'
import { FlowdaTrpcClientSymbol, PrismaClientSymbol } from '@flowda-projects/flowda-shared'
import { JwtService } from '../jwt/jwt.service'
import { IConfigService, IMailService } from '../../infra'
import { App, PrismaClient } from '@prisma/client-v1-flowda'
import { AuthenticationService, excludedIdentity, TExcludedIdentity } from '../authentication/authentication.service'
import keymachine from 'keymachine'
import * as bcrypt from 'bcrypt'
import { AppRegisterResDto } from './dto/appRegisterRes.dto'
import { AppCreateDto } from './dto/dto'
import { SignupDto } from './dto/signup.dto'
import { CreateTRPCProxyClient } from '@trpc/client'
import { AppRouter } from '@flowda-projects/flowda-services-trpc-server'

@injectable()
export class AppAuthService extends AuthenticationService<App> {
  constructor(
    @inject(IdentityProviderServiceSymbol)
    @named('app')
    protected identityProvider: IIdentityProviderService<App>,
    @inject(JwtService) protected jwt: JwtService,
    @inject(IConfigService) protected config: IConfigService,
    @inject(IMailService) protected mailService: IMailService,
    @inject(PrismaClientSymbol) protected prisma: PrismaClient,
    @inject(FlowdaTrpcClientSymbol) private flowdaTrpc: CreateTRPCProxyClient<AppRouter>,
  ) {
    super(identityProvider, jwt, config, mailService)
  }

  @postConstruct()
  postConstruct() {
    this.setOptions({
      access_token_secret: this.config.getEnv('app_access_token_secret'),
      refresh_token_secret: this.config.getEnv('app_refresh_token_secret'),
      access_token_expire: this.config.getEnv('app_access_token_expire'),
      refresh_token_expire: this.config.getEnv('app_refresh_token_expire'),
    })
  }

  async create(dto: AppCreateDto): Promise<AppRegisterResDto> {
    const randomAppId = this.generateRandomUppercaseLetter(4)
    const randomAppToken = keymachine()
    const user = await this.signup(
      {
        name: randomAppId,
        password: randomAppToken,
        tenantId: dto.tenantId,
      },
      dto,
    )

    return {
      id: user.id,
      appId: user.name,
      appToken: randomAppToken, // appToken 
      displayName: user.displayName,
      description: user.description,
    }
  }

  public async validate(appId: string, appToken: string) {
    const tokens = await super.validateUserReturnTokens('name', appId, appToken)
    return {
      at: tokens.at,
      rt: tokens.rt,
      expireAt: tokens.expireAt,
      app: tokens.user,
    }
  }

  public async validateV4(appId: string, appToken: string) {
    const tenant = await this.flowdaTrpc.user.getTenantByName.query({
      tenantName: appId,
    })
    const { at, exp } = this.jwt.generateAccessToken(String(tenant.id), {
      secret: this.options.access_token_secret,
      exp: this.options.access_token_expire,
    })
    return {
      at,
      rt: null,
      app: this.v4ConvertTo(tenant),
      expireAt: exp,
    }
  }

  /**
   *  tenant ->  app 
   * @param tenant
   * @private
   */
  private v4ConvertTo(tenant: any) {
    return {
      id: tenant.id,
      name: tenant.name,
      displayName: tenant.name,
      description: tenant.name,
    }
  }

  async getUserV4(userId: string): Promise<any> {
    this.logger.debug!(`[getUserV4] userId ${userId}`)
    const tenant = await this.flowdaTrpc.user.getTenant.query({
      tid: Number(userId),
    })
    if (!tenant) {
      throw new AuthenticationError.AccountNotFound()
    }
    return this.v4ConvertTo(tenant)
  }

  async appRefreshToken(rt: string): Promise<{ at: string; expireAt: number; app: TExcludedIdentity<App> }> {
    const ret = await super.refreshToken(rt)
    return {
      at: ret.at,
      expireAt: ret.expireAt,
      app: ret.user,
    }
  }

  async signup(dto: SignupDto, extraFields: Record<string, any> = {}): Promise<TExcludedIdentity<App>> {
    const app = await this.identityProvider.find({
      name: dto.name,
      tenantId: dto.tenantId,
    })
    if (app) {
      throw new AuthenticationError.AccountNameAlreadyExists()
    }

    let hashedPassword: string | null = null
    if (dto.password) {
      // todo: /
      hashedPassword = await bcrypt.hash(dto.password, 10)
    }

    // const { password, ...rest } = dto //  password
    const a = {
      ...dto, //  passwordidentity  identity 
      hashedPassword: hashedPassword,
      hashedRefreshToken: null,
      recoveryCode: null,
      recoveryToken: null,
      ...extraFields,
    }
    const newApp = await this.identityProvider.create(a)

    return excludedIdentity<App>(newApp)
  }
}
