import { PassportStrategy } from '@nestjs/passport'
import { ExtractJwt, Strategy } from 'passport-jwt'
import { Injectable, Logger } from '@nestjs/common'
import { AppAuthService } from '@flowda-projects/v1-flowda-services'

@Injectable()
export class AppJwtV4Strategy extends PassportStrategy(Strategy, 'appJwtV4') {
  private readonly logger = new Logger(AppJwtV4Strategy.name)

  constructor(private service: AppAuthService) {
    const at = ExtractJwt.fromAuthHeaderAsBearerToken()
    super({
      jwtFromRequest: at,
      ignoreExpiration: false,
      secretOrKey: service.getAccessTokenSecret(),
    })
  }

  async validate(payload: any) {
    this.logger.debug!(`[validate] payload, ${JSON.stringify(payload)}`)
    const app = await this.service.getUserV4(payload.uid)
    this.logger.debug!(`[validate] app, ${JSON.stringify(app)}`)
    return app
  }
}
