import { interfaces } from 'inversify'
import { flowdaInfraModule, flowdaServicesModule, prismaClientFlowdaModule } from '@flowda-projects/v1-flowda-services'
import { flowdaSharedModule, FlowdaTrpcClientSymbol } from '@flowda-projects/flowda-shared'
import { flowdaSharedNodeModule } from '@flowda-projects/flowda-shared-node'
import { CreateTRPCProxyClient } from '@trpc/client'
import { AppRouter } from '@flowda-projects/flowda-services-trpc-server'
import { trpc } from './trpc/trpc'
import { CMS_ADMIN_ENV } from '@flowda-projects/cms-admin-services'

console.log('---------- ENV --------------')
console.log('FLOWDA_URL', process.env.FLOWDA_URL)
console.log('---------- ENV --------------')

export function loadModule(container: interfaces.Container) {
  container.bind<CreateTRPCProxyClient<AppRouter>>(FlowdaTrpcClientSymbol).toConstantValue(trpc)

  container.load(flowdaSharedModule)
  container.load(flowdaSharedNodeModule)
  container.load(prismaClientFlowdaModule)
  container.load(flowdaInfraModule)
  container.load(flowdaServicesModule)
}
