import { PassportStrategy } from '@nestjs/passport'
import { Strategy } from 'passport-local'
import { Injectable, Logger, UnauthorizedException } from '@nestjs/common'
import { AppAuthService } from '@flowda-projects/v1-flowda-services'

@Injectable()
export class AppLocalAuthV4Strategy extends PassportStrategy(Strategy, 'appLocalV4') {
  private readonly logger = new Logger(AppLocalAuthV4Strategy.name)

  constructor(private authService: AppAuthService) {
    super({
      usernameField: 'appId',
      passwordField: 'appToken',
    })
  }

  async validate(appId: string, appToken: string): Promise<any> {
    this.logger.debug!('validate')
    const tokens = await this.authService.validateV4(appId, appToken)
    if (!tokens) {
      throw new UnauthorizedException()
    }
    return tokens
  }
}
